@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;600;800&display=swap');

:root {
	--colorbg_correct: #94D7A2;
	--colorbg_incorrect: #F8BCBC;
	/* --selected: lineargrad */
	--bg: #DEF5E5;
	--held: #0eb88584;
	--blue_hard: #4D5B9E;
	--blue_light: #293264;
}

*,
*::before,
*::after {
	box-sizing: border-box;
	margin: 0;
}

html,
body {
	background-color: #4158D0;
	background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
	line-height: 1.5;
	-webkit-font-smoothing: antialiased;
	/* height: 100%; */

}


picture,
video,
canvas,
svg {
	display: block;
	max-width: 100%;
}

input,
button,
textarea,
select {
	font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
	overflow-wrap: break-word;
}

img{cursor: pointer;
	/* width: 20%; */
	height: 5rem;
	border-radius: 20%;
}
nav{
	height: 6rem;
	background-color: white;
	position: fixed;
	display: block;
	top: 0;
	left: 0;
	display: flex;
	padding: 2rem;
	justify-content: space-between;
	width: 100%;
	align-items: center;

}
h1 {
	font-size: 3rem;
	background: -webkit-linear-gradient(#C850C0, #0e0e0e);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;

}
.about{
text-align: center;
}
a{
	text-decoration: none;
}
.close{
	width: 20%;
	margin:1rem auto;
}
h2 {
	font-size: 1.2rem;
	color: black;
}

p {
	font-size: 1rem;
	color: black;
}

#root,
#__next {
	/* align-items: center; */
	isolation: isolate;
	display: flex;
	justify-content: center;
}

body {
	width: 100vw;
	height: 100%;
	margin: auto;
	/* overflow: hidden; */
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'Roboto Slab', serif;
}

.App {
	width: 70%;

	padding-top: 5rem;
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

}

.start {
	margin-bottom: 2rem;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

