.answer.selected {
	border-color: var(--held);
	background-color: var(--held);
}

.answer.correct {
	border-color: var(--colorbg_correct);
	background-color: var(--colorbg_correct);
}

.answer.incorrect {
	border-color: var(--colorbg_incorrect);
	background-color: var(--colorbg_incorrect);
}

button {
	color: cadetblue;
	cursor: pointer;
	padding: 0 2rem;
	border-radius: 5px;
	margin-top: 1rem;
}

.quiz {
	background-color: white;
	display: flex;
	gap: 1rem;
	flex-direction: column;
	justify-content: center;


	border: 1px solid gray;
	padding: 2rem 4rem;
	border-radius: 10px;
}

.answer {
	box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
	display: flex;
	justify-content: center;
	width: 40%;
	border: 1px solid gray;
	padding: 0 1rem;
	border-radius: 5px;
	cursor: pointer;
	/* height: 2rem; */
}

.answer:hover {
	scale: 1.1;
}

.form {
	color: #031414;
	width: 20rem;
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
}

.form label {
	width: 100%;
}

label input {
	width: 100%;
	height: 1.5rem;
}

.answers {
	margin-top: 1rem;
	display: flex;
	gap: 1rem;
	/* width: 40%; */
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.results {
	width: 100%;
	justify-content: center;
	flex-direction: column;
	display: flex;
	align-items: center;
}

.quizs {
	display: flex;
	justify-content: center;
	flex-direction: column;
}

.modal-container {
	background-color: #9cbfa792;
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 200vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.vis {
	visibility: hidden;
}

.modal {
	/* width: 20rem; */
	height: 15rem;
	background-color: #DEF5E5;
	position: absolute;
	top: 10%;
	padding: 2em;
	display: flex;
	justify-content: center;
	flex-direction: column;
}

@media screen and (max-width:600px) {
	.answer {
		width: 100%;
	}
	.close{
		width: 40%;
	}
	.App {
		width: 100%;
	}

	.quiz {
		padding: 1rem 2rem;
	}
}